import React from 'react';
import {Panel, PanelHeader, HeaderButton, platform, IOS, Div, Button, Spinner} from '@vkontakte/vkui';
import './Persik.css';
import Icon28ChevronBack from '@vkontakte/icons/dist/28/chevron_back';
import Icon24Back from '@vkontakte/icons/dist/24/back';
const osname = platform();

const Persik = (props,up,down,score,visiblebtn,statusa,loading) => (
	<Panel id={props.id}>
		<PanelHeader
			left={<HeaderButton onClick={props.go} data-to="home">
				{osname === IOS ? <Icon28ChevronBack/> : <Icon24Back/>}
			</HeaderButton>}
		>
			Счет: { props.score }
		</PanelHeader>
		{ props.loading === false &&
		<div className="container-fluid">
			<div className="row">
				<div className="col-md-6">
					<div className="game_card shadow" style={{ backgroundImage: `url(${props.imgone})` }}>
						<div className="game_info">
							<h1 className="game_name">{props.nameone}</h1>
							<span className="game_dop">имеет</span>
							<h2 className="game_views">{props.viewsone}</h2>
							<span className="game_request">запросов в месяц</span>
						</div>
					</div>
				</div>
				<h1 className="game_vs">VS</h1>
				{ props.visiblebtn === false &&
					<Div className="status">	
				{ props.statusa === true &&
					<h1 className="status">Верно!</h1>
				}
					</Div>
				}
				<div className="col-md-6">
					<div className="game_card shadow" style={{ backgroundImage: `url(${props.imgtwo})` }}>
						<div className="game_info">
							<h1 className="game_name">{props.nametwo}</h1>
							<span className="game_dop">имеет</span>
							{ props.visiblebtn === true &&
							<Div>
								<Button className="selectbtn" level="commerce" onClick={props.up}>Больше</Button>
								<Button className="selectbtn" level="destructive" onClick={props.down}>Меньше</Button>
							</Div>
							} { props.visiblebtn === false &&
								<h2 className="game_views">{props.viewstwo}</h2>
							}
							<span className="game_request">запросов в месяц</span>
						</div>
					</div>
				</div>
			</div>
		</div>
		} { props.loading === true &&
		<div style={{ display: 'flex', alignItems: 'center', flexDirection: 'column' }}>
		  <Spinner size="large" style={{ marginTop: 20 }} />
		</div>
		}
	</Panel>

);


export default Persik;
